import { Component, OnInit, Inject } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ArticlesService } from 'src/app/services/uiServices/articles.service';
import { DomSanitizer } from '@angular/platform-browser';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Constants } from 'src/app/Constants';
import { MetaService } from '@ngx-meta/core';
import { PLATFORM_ID } from '@angular/core';
import {isPlatformServer } from '@angular/common';

@Component({
  selector: 'app-article-details',
  templateUrl: './article-details.component.html',
  styleUrls: ['./article-details.component.css'],
})
export class ArticleDetailsComponent implements OnInit {
  articleId: string;
  articleType: string;
  selectedArticle: Article = {
    article: "",
    caption: "",
    images: [""],
    publishDate: "",
    publishTime: "",
    videos: "",
    isShowen: false,
    writer: "",
    name: ""
  };
  articleBody: any;
  articles: Array<Article>;
  showCarousel: boolean = false;
  articlesCount: number;
  sharingLink: string = "";
  constructor(
    private route: ActivatedRoute,
    private articlesServices: ArticlesService,
    private sanitizer: DomSanitizer,
    private location: Location,
    private router: Router,
    @Inject(PLATFORM_ID) private platformId: Object,
    private readonly meta: MetaService) {
    this.articlesCount = 5;
    this.route.params.subscribe(params => {
      this.articleId = params.id;
      this.articleType = params.type;
      this.getArticleDetails();
      this.showCarousel = false;
      this.loadArticles();
    });
    // console.log(this.router.url);
    this.sharingLink = Constants.SHARING_URL + this.router.url;
  }

  ngOnInit() {


    // console.log(this.sharingLink);
  }
  async getArticleDetails() {
    this.selectedArticle = await this.articlesServices.getArticleDetails(this.articleId, this.articleType == "sports");
    // if (isPlatformServer(this.platformId)) {
    // this.meta.setTag('og:title',this.selectedArticle.writer );
    // this.meta.setTag('og:description', this.selectedArticle.name);
    // this.meta.setTag('og:image', this.selectedArticle.images[0]);
    // this.meta.setTag('twitter:title', this.selectedArticle.writer);
    // this.meta.setTag('twitter:description', this.selectedArticle.name);
    // this.meta.setTag('og:image', this.selectedArticle.images[0]);
    // console.log(this.selectedArticle);
    // }
    this.selectedArticle.publishTime = this.selectedArticle.publishTime.substr(this.selectedArticle.publishTime.indexOf("T") + 1, 5);
    this.articleBody = this.sanitizer.bypassSecurityTrustHtml(this.selectedArticle.article);
    this.showCarousel = true;
  }
  async loadArticles() {
    this.articles = await this.articlesServices.getArticlesList(this.articleType == "sports", this.articlesCount);
    this.articles = this.articles.filter((article) => article.isShowen && article.id != this.articleId);
    this.articles = this.articles.map((article) => {
      let newArticle: any = article;
      newArticle.article = this.sanitizer.bypassSecurityTrustHtml(article.article);
      return newArticle;
    });
  }
  async loadMore() {
    this.location.back();
  }
  ngOnDestroy() {
    // this.meta.setTag('og:title', "قناة الوسط الليبية");
    // this.meta.setTag('og:description', "قناة تلفزيونية متنوعة .. تطمح لتقديم مايخدم الهوية الليبية في إطار فني ثقافي متميز");
    // this.meta.setTag('og:image', "http://wasat.tv/home/assets/imgs/logo.png");
    // this.meta.setTag('twitter:title', "قناة الوسط الليبية");
    // this.meta.setTag('twitter:description', "قناة تلفزيونية متنوعة .. تطمح لتقديم مايخدم الهوية الليبية في إطار فني ثقافي متميز");
    // this.meta.setTag('og:image', "http://wasat.tv/home/assets/imgs/logo.png");
  }
  getSharingLink(articleId: string): string {
    return Constants.SHARING_URL + "/articleDetails/" + this.articleType + "/" + articleId;
  }
}
